@import "params.scss";
#header{
  height:6rem;
  width:100%;
  background: $commonColor;
  color:rgba(255,255,255,0.8);
  font-weight: 200;
  position: relative;
  .header_img{
    height:3rem;
    width:3rem;
    border: 1px solid white;
    border-radius: 50%;
    left:30px;
    @include center;
  }
  .header_container{
    height:3rem;
    width:calc(100% - 153px);
    left:150px;
    @include center;
    .header_title{
      font-size:1rem;
    }
    .header_info{
      position: absolute;
      bottom:0;
      font-size:0.5rem;
    }
  }
}
