$height:40px;
@import "params.scss";
// $commonColor:rgb(54,91,206);
.footer{
  height:$height;
  width:100%;
  background: rgba(0,0,0,0.4);
  position: fixed;
  bottom: 0;
  color:white;
  .shopCarIcon{
    height:$height;
    width: $height;
    border-radius: 50%;
    border: 1px solid $commonColor;
    background: white;
    color:$commonColor;
    text-align: center;
    line-height: $height;
    font-weight: 600;
    transform:translateX(10px) translateY(-30%);
    span{
      color:white;
      background: $commonColor;
      border-radius: 50%;
      height:16px;;
      width:16px;;
      line-height: 16px;;
      text-align: center;
      font-size:12px;
      z-index: 999;
      position: absolute;
    }
  }
  .totle{
    left:60px;
    font-weight: 100;
    @include center;
  }
  .confirm{
    height:$height;
    width:90px;
    background: $commonColor;
    position: absolute;
    right:0;
    top:0;
    border:none;
    color:white;
  }
}
