#shopCar{
  width:100vw;
  // outline:1px solid red;
  background: white;
  position: absolute;
  bottom:40px;
  .top{
    background: rgb(240,240,240);
    height:30px;
    line-height: 30px;
    padding:5px;
    font-size: 14px;
    padding-left: 10px;

  }
  li{
    line-height: 30px;
    font-size:14px;
    position: relative;
    padding:10px 5px 10px 5px;
    .bottom{
      width:80%;
      height:1px;
      background: rgba(0,0,0,0.2);
      position: absolute;
      bottom: 0;
      left:50%;
      transform: translateX(-50%);
    }
    div{
      display: inline-block;
    }
    .shopName{
      font-weight: 700;
    }
    .right{
      position: absolute;
      right:10px;
      // outline: 1px solid red;
    }
    .money{
      // outline: 1px solid red;
      margin-right: 10px;
      color:rgb(250,125,67);
      font-weight: 100;
    }
    .iconfont{
      height: 25px;
      width: 25px;
      text-align: center;
      line-height: 25px;
      color:rgba(0,0,0,0.3);
      font-weight: 100;

    }
    .add,.reduce{
      border:none;
      background: rgb(213,226,255);
      border-radius: 50%;
    }
    .counter{
      div{
        display: inline-block;
      }
      .number{
        // font-weight: 600;
        color:rgba(0,0,0,.56);
        // outline: 1px solid red;
      }
      .add{

      }
      .reduce{

      }
    }
  }

}
