@import "params.scss";
.split{
  width:100%;
  height:30px;
  border-bottom: 1px solid rgba(54,91,206,0.2);
  li{
    display: inline-block;
    text-align: center;
    line-height: 30px;
    color:$commonColor;
    // font-size: 12px;
    &:nth-child(1){
      width:2.5rem;
      background: #eee;
    }
    &:nth-child(2){
      width:calc(100% - 2.5rem);
    }
  }
}
