@import "params.scss";
#container{
  // overflow-y: scroll;
  overflow-y: auto;
  height: calc(100vh - 6rem - 40px);
  margin-top: 40px;
  width: calc(100% - 2.5rem);
  // border: 1px solid red;
  // outline: 1px solid red;
  position: fixed;
  left:2.5rem;
  top:6rem;
  li{
    width: inherit;
    height:100px;
    // outline: 1px solid red;
    position: relative;
    padding: 0;
    border-bottom: 1px solid rgba(0,0,0,.2);
  }
  .left{
    width: 100px;
    height:inherit;
    margin-right: 0;
    position: relative;
    // outline: 1px solid red;
    img{
      height:70px;
      width: 70px;
      display: inline-block;
      position: absolute;
      top:50%;
      left:20px;
      transform: translateY(-50%);
    }
  }
  .right{
    @include center;
    left:120px;
    width:calc(100% - 120px);
    // outline: 1px solid red;
    .title{
      font-size:14px;
      color:$commonColor;
      font-weight: 600;
    }
    .sales{
      font-size:12px;
      display: inline;
    }
    .money{
      font-size: 17px;
      font-weight: 500;
      letter-spacing:1px;
    }
    .iconfont{
      height: 25px;
      width: 25px;
      text-align: center;
      line-height: 25px;
      color:rgba(0,0,0,0.3);
      font-weight: 100;
    }
    .add,.reduce{
      border:none;
      background: rgb(213,226,255);
      border-radius: 50%;
    }
    .counter{
      // outline: 1px solid red;
      @include center;
      right:25px;

      .add{
        // right:25px;
      }
    }
    .counterChoose,.counter{
      height:25px;
    }
    .counterChoose{
      // display: none;
      // text-align: center;
      // @include center;
      line-height: 25px;
      // outline: 1px solid red;
      width:auto;
      @include center;
      right:50px;
      .iconfont{
        display: inline-block;
      }
      .number{
      }
      .add{
        position: absolute;
        float: right;
      }
      .reduce{
        float: left;
      }
    }
  }
}
